<template>
  <div>
    <swipe class="my-swipe" direction="horizontal">
      <swipe-item class="slide1"><img :src="img_slide1" alt="slide1"></swipe-item>
      <swipe-item class="slide2"><img :src="img_slide2" alt="slide2"></swipe-item>
      <swipe-item class="slide3"><img :src="img_slide3" alt="slide3"></swipe-item>
    </swipe>
  </div>
</template>

<script>
import img_slide1 from '@/assets/slides/slide-1.png'
import img_slide2 from '@/assets/slides/slide-2.png'
import img_slide3 from '@/assets/slides/slide-3.png'

require('vue-swipe/dist/vue-swipe.css')
import { Swipe, SwipeItem } from 'vue-swipe'

export default {
  data() {
    return {
      img_slide1,
      img_slide2,
      img_slide3
    };
  },
  components: {
    swipe: Swipe,
    swipeItem: SwipeItem
  },
  methods: {
    changeSwipe(newIndex, oldIndex) {
      console.log(`swipe from ${oldIndex} to ${newIndex}`);
    }
  }
};
</script>

<style scoped>
.my-swipe {
  height: 300px;
  color: #fff;
  font-size: 30px;
  text-align: center;
}

.slide1 {
  background-color: #0089dc;
  color: #fff;
}

.slide2 {
  background-color: #ffd705;
  color: #000;
}

.slide3 {
  background-color: #ff2d4b;
  color: #fff;
}

img {
  width: 100%;
  height: 300px;
}
</style>



