<template>
  <div>
    <el-row>
      <el-col :span="24">
        <Carousel></Carousel>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <div class="text-content">
          <p>CHENGDU’S FAVOURITE MOVING HOUSE SERVICE SEEKING WEBSITE</p>
          <p>成都最受欢迎的搬家公司服务网站</p>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center">
      <el-col :span="12" class="sub_container">
        <PreSales></PreSales>
      </el-col>
      <el-col :span="12" class="sub_container margin_left0">
        <GetPrice></GetPrice>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Carousel from '@/components/carousel'

import GetPrice from '@/components/getprice'
import PreSales from '@/components/presales'

export default {
  name: 'app',
  components: {
    Carousel,
    PreSales,
    GetPrice
  }
}
</script>

<style>
.el-row {
  margin-bottom: 0;
}

.bg-purple-dark {
  background: #99a9bf;
}

.bg-purple-light {
  background: #e5e9f2;
}

.grid-content {
  min-height: 36px;
}

.text-content {
  padding: 16px;
  font-size: 28px;
  text-align: center;
}

.sub_container {
  margin-left: 20px;
  margin-right: 20px;
  min-height: 256px;
  border-radius: 2px;
  background: #0089dc;
}

.margin_left0 {
  margin-left: 0px;
}
</style>



